import { createDoc } from "../api/api";
export default {
  name: "collection",
  data: function data() {
    return {
      chart: ""
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      if (localStorage.getItem('chart')) {
        this.chart = localStorage.getItem('chart');
      } else {
        this.chart = '图标';
      }
    },
    toNewDoc: function toNewDoc() {
      var _this = this;

      createDoc(0).then(function (res) {
        if (res.status === 201) {
          _this.$message({
            message: '新建文档成功',
            type: 'info'
          });

          _this.$router.push({
            name: 'editorPage',
            params: {
              docId: res.data.id
            }
          });
        }
      }).catch(function (e) {
        if (e.response.status === 401) {
          _this.$message({
            message: '您没有权限',
            type: 'error'
          });
        }
      });
    },
    changeChart: function changeChart(value) {
      this.chart = value;
      localStorage.setItem('chart', value);
    },
    toTemplate: function toTemplate() {
      this.$router.push({
        name: 'templates'
      });
    }
  }
};